.itemsViewContainer {
    min-width: 80vw;
    min-height: 93vh;
    padding: 3vh 9vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid grey;
    background: #F2F2F2;
}

.itemsBlock {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 10vh;
}

.buttonContainer {
    width: 235px;
    height: 60px;
    text-align: center;
    font-size: 24px;
    color: #50DAA8;
    border: solid 1px grey;
    border-radius: 16px;
    background-color: #ffffff;
}

.buttonContainer:hover {
    background-color: #50DAA8;
    color: #ffffff;
}

.buttonContainer:disabled {
    background-color: #D3D3D3;
    color: #F2F2F2;
}
